.top {

    &__inner {
        padding-bottom: 70px;
        position: relative;
        &::after{
            content: '';
            background-image: url('../img/content/jpg/slider-bg.jpg');
            background-repeat: no-repeat;
            position: absolute;
            width: 100%;
            height: 722px;
            top: 0;
            z-index: -1;
        }
    }

    &__slider-item {
        display: flex !important;
        justify-content: center;
        align-items: center;
    }

    &__slider-img {
        margin-left: -14%;
        max-width: 703px;
        max-height: 815px;
        object-fit: cover;
    }

    &__slider-info {
        max-width: 645px;
        margin-left: 25px;
    }

    &__slider-title {
        color: $black-title;
        font-size: 110px;
        line-height: 135px;
        padding-bottom: 26px;
        margin-bottom: 26px;
        position: relative;
        @extend %title-font;
        &::after{
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            width: 185px;
            height: 1px;
            background: #6d7278;
        }
    }

    &__slider-text {
        max-width: 585px;
    }
}

.conteiner-fluid {
    max-width: 1440x;
    padding: 0 10px;
    }

